import {
	Component,
	OnInit,
	Input,
	Output,
	EventEmitter
} from "@angular/core";
import {
	ReactiveFormsModule,
	FormBuilder,
	FormGroup,
	FormControl
} from "@angular/forms";

import { storiesOf, moduleMetadata } from "@storybook/angular";
import { action } from "@storybook/addon-actions";
import {
	withKnobs,
	select,
	boolean,
	object,
	text
} from "@storybook/addon-knobs/angular";

import { DropdownModule, DocumentationModule } from "../";
import { of } from "rxjs";
import { PlaceholderModule } from "../placeholder/placeholder.module";

const getProps = (overrides = {}) => Object.assign({}, {
	invalid: boolean("Invalid", false),
	invalidText: "This is not a validation text",
	disabled: boolean("disabled", false),
	label: text("Label", "Dropdown label"),
	helperText: text("Helper text", "Optional helper text."),
	items: object("items", [
		{ content: "one" },
		{ content: "two" },
		{ content: "three" },
		{ content: "four" }
	]),
	selected: action("Selected fired for dropdown"),
	onClose: action("Dropdown closed"),
	theme: select("theme", ["dark", "light"], "dark")
}, overrides);

@Component({
	selector: "app-reactive-forms",
	template: `
		<form [formGroup]="formGroup">
			<div style="text-align:center">
				<ibm-dropdown
					[label]="label"
					[helperText]="helperText"
					[invalid]="invalid"
					[invalidText]="invalidText"
					[theme]="theme"
					type="multi"
					placeholder="Multi-select"
					value="oid"
					(selected)="selected.emit($event)"
					(onClose)="onClose.emit($event)"
					formControlName="roles">
					<ibm-dropdown-list [items]="items"></ibm-dropdown-list>
				</ibm-dropdown>
			</div>
		</form>

		<br>

		<code>{{ formGroup.get("roles").value | json }}</code>
	`
})
class ReactiveFormsStory implements OnInit {
	public formGroup: FormGroup;

	@Input() items = [];
	@Input() label = "";
	@Input() helperText = "";
	@Input() invalid = false;
	@Input() invalidText = "";
	@Input() set disabled(value) {
		if (!this.formGroup) { return; }
		if (value) {
			this.formGroup.get("roles").disable();
		} else {
			this.formGroup.get("roles").enable();
		}
	}
	@Output() selected = new EventEmitter();
	@Output() onClose = new EventEmitter();

	constructor(protected formBuilder: FormBuilder) {}

	ngOnInit() {
		this.formGroup = this.formBuilder.group({
			roles: new FormControl()
		});
		this.selectRoles();
	}

	private selectRoles() {
		this.formGroup.get("roles").setValue([1, 2]);
	}
}

