import { Component } from "@angular/core";
import { storiesOf, moduleMetadata } from "@storybook/angular";
import { withKnobs } from "@storybook/addon-knobs/angular";
import {
    TableModule,
    TableModel,
    TableHeaderItem,
    TableItem } from "../../table/table.module";
import { DropdownModule } from "../../dropdown/dropdown.module";
import { GridModule } from "../../grid/grid.module";
import { ButtonModule } from "../../forms/forms.module";
import { UIShellModule } from "../../ui-shell/ui-shell.module";

@Component({
    selector: "app-sample-progressive-loading",
    template: `
    <div ibmGrid>
        <div ibmRow class="header">
            <ibm-header name="Patterns">
                <ibm-hamburger></ibm-hamburger>
            </ibm-header>
        </div>
        <div ibmRow class="actions">
            <div ibmCol [columnNumbers]="{'lg': 3, 'md': 2, 'sm': 1}">
                <ibm-dropdown
                    label="Example 1"
                    [skeleton]="skeletonStateDropdown"
                    type="multi"
                    [placeholder]="placeholder"
                    inline="true">
                    <ibm-dropdown-list [items]="items"></ibm-dropdown-list>
                </ibm-dropdown>
            </div>

            <div ibmCol [columnNumbers]="{'lg': 3, 'md': 2, 'sm': 1}">
                <ibm-dropdown
                    label="Example 2"
                    [skeleton]="skeletonStateDropdown"
                    type="multi"
                    [placeholder]="placeholder"
                    inline="true">
                    <ibm-dropdown-list [items]="items"></ibm-dropdown-list>
                </ibm-dropdown>
            </div>

            <div ibmCol [columnNumbers]="{'lg': 3, 'md': 2, 'sm': 1}">
                <ibm-dropdown
                    label="Example 3"
                    [skeleton]="skeletonStateDropdown"
                    type="multi"
                    [placeholder]="placeholder"
                    inline="true">
                    <ibm-dropdown-list [items]="items"></ibm-dropdown-list>
                </ibm-dropdown>
            </div>

            <div ibmCol [columnNumbers]="{'lg': 3, 'md': 2, 'sm': 1}">
                <button ibmButton (click)="loadScreen(); uninitializeData()">Show Loading</button>
            </div>
        </div>
        <div ibmRow>
            <div ibmCol [columnNumbers]="{'lg': 12, 'md': 12, 'sm': 12}">
                <ibm-table-container>
                    <ibm-table
                        class="data-table"
                        [skeleton]="skeletonStateTable"
                        [model]="model"
                        size="lg"
                        [showSelectionColumn]="false">
                        <ng-content></ng-content>
                    </ibm-table>
                </ibm-table-container>
            </div>
        </div>
    </div>
    `,
    styles: [`
        .header {
            margin-bottom: 80px;
        }

        .data-table {
            width: 100%;
        }

        .actions {
            margin-bottom: 40px;
        }
        `
    ]
})
class SampleProgressiveLoading {   
    model = new TableModel();
    skeletonStateTable = true;
    skeletonStateDropdown = true;
    placeholder = null;

    dataset = [
        { name: "Apple", type: "Fruit" },
        { name: "Grape", type: "Fruit" },
        { name: "Eggplant", type: "Fruit" },
        { name: "Lettuce", type: "Vegetable" },
        { name: "Daikon Radish", type: "Vegetable" },
        { name: "Beef", type: "Meat" }
    ];

    items = [
        { content: "Vegetable" },
        { content: "Fruit" },
        { content: "Meat" }
    ];

    uninitializeData() {
        this.skeletonStateTable = true;
        this.skeletonStateDropdown = true;
        this.placeholder = null;
    }

    loadScreen() {
        this.model.data = this.dataset.map(datapoint => [new TableItem({}), new TableItem({})]);
        
        this.model.header = [new TableHeaderItem({ data: "" }), new TableHeaderItem({ data: "" })];

        setTimeout(() => {
            this.skeletonStateDropdown = false;
            this.placeholder = "Type";
        }, 2000);

        setTimeout(() => {
            this.skeletonStateTable = false;
        }, 4000);

        setTimeout(() => {
            this.model.header = [new TableHeaderItem({ data: "Name" }), new TableHeaderItem({ data: "Description" })];

            this.model.data = this.dataset.map(datapoint => 
                [
                    new TableItem({ data: datapoint.name }),
                    new TableItem({ data: "Lorem ipsum dolor sit amet, consectetur adipiscing elit." })
                ]
            );
        }, 4000);
    }

    ngOnInit() {
        document.querySelector('.sb-show-main').classList.add('full-page');
        this.loadScreen();
    }

    ngOnDestroy() {
        document.querySelector('.sb-show-main').classList.remove('full-page');
    }
}

storiesOf("Patterns|Loading", module).addParameters({ storySource: { source: __STORY__, locationsMap: __ADDS_MAP__ } }).addDecorator(withSourceLoader(__STORY__, __ADDS_MAP__,__MAIN_FILE_LOCATION__,__MODULE_DEPENDENCIES__,__LOCAL_DEPENDENCIES__,__SOURCE_PREFIX__,__IDS_TO_FRAMEWORKS__))
	.addDecorator(
		moduleMetadata({
			declarations: [ SampleProgressiveLoading ],
			imports: [
				TableModule,
				DropdownModule,
				GridModule,
				ButtonModule,
				UIShellModule
			]
		})
	)
	.addDecorator(withKnobs)
	.add("Progressive Loading", () => ({
        template: `<app-sample-progressive-loading></app-sample-progressive-loading>`
    }))
